<style lang="scss" scoped>
  .plugins-tips {
    background: #eef1f6;
    padding: 20px 10px;
    a {
      color: #20a0ff!important;
    }
  }
  .swiper-slide {
    height: 300px;
    background: #eee;
    text-align: center;
    font-size: 24px;
    line-height: 300px;
  }
  pre {
    white-space: pre;
    overflow-x: auto;
  }
  .swiperOption2 {
    position: relative;
    overflow: hidden;
    height: 330px;
    padding: 15px;
    .swiper-container {
      width: 300px!important;
      height: 300px;
      position: absolute;
      left: 50%;
      top: 50%;
      margin-left: -150px;
      margin-top: -150px;
    }
  }

  .swiperOption3 {
    .swiper-slide {
      width: 300px;
    }
  }

  .el-carousel__item {
    text-align: center;
  }
  .el-carousel__item h3 {
    color: #475669;
    font-size: 14px;
    opacity: 0.75;
    line-height: 200px;
    margin: 0;
  }
  .el-carousel__item:nth-child(2n) {
     background-color: #99a9bf;
  }
  .el-carousel__item:nth-child(2n+1) {
     background-color: #d3dce6;
  }
</style>

<template>
  <div>
    <el-breadcrumb separator="/">
      <el-breadcrumb-item><i class="el-icon-date"></i> 案例</el-breadcrumb-item>
      <el-breadcrumb-item>轮播图</el-breadcrumb-item>
    </el-breadcrumb>

    <div class="plugins-tips mt-20 mb-20 border-radius3">
      vue-awesome-swiper:用于Vue的Swiper（Swiper3）组件,支持pc和移动,SPA和SSR。
      访问地址：<a href="https://www.npmjs.com/package/vue-awesome-swiper" target="_blank">vue-awesome-swiper</a>
    </div>

    <swiper :options="swiperOption">
      <swiper-slide>Slide 1</swiper-slide>
      <swiper-slide>Slide 2</swiper-slide>
      <swiper-slide>Slide 3</swiper-slide>
      <swiper-slide>Slide 4</swiper-slide>
      <swiper-slide>Slide 5</swiper-slide>
      <swiper-slide>Slide 6</swiper-slide>
      <div class="swiper-pagination" slot="pagination"></div>
      <div class="swiper-button-prev" slot="button-prev"></div>
      <div class="swiper-button-next" slot="button-next"></div>
    </swiper>

    <pre class="mt-20"><code>
      &lt;swiper :options="swiperOption"&gt;
        &lt;swiper-slide&gt;Slide 1&lt;/swiper-slide&gt;
        &lt;swiper-slide&gt;Slide 2&lt;/swiper-slide&gt;
        &lt;swiper-slide&gt;Slide 3&lt;/swiper-slide&gt;
        &lt;swiper-slide&gt;Slide 4&lt;/swiper-slide&gt;
        &lt;swiper-slide&gt;Slide 5&lt;/swiper-slide&gt;
        &lt;swiper-slide&gt;Slide 6&lt;/swiper-slide&gt;

        &lt;div class="swiper-pagination" slot="pagination"&gt;&lt;/div&gt;
        &lt;div class="swiper-button-prev" slot="button-prev"&gt;&lt;/div&gt;
        &lt;div class="swiper-button-next" slot="button-next"&gt;&lt;/div&gt;
      &lt;/swiper&gt;

      data() {
        return {
          swiperOption: {
            pagination: '.swiper-pagination',   //显示分页
            paginationClickable: true,          //分页可点击
            direction: 'horizontal',            //vertical
            spaceBetween: 0,                    // 在Slides之间产生间隙
            slidesPerView: 1,                   //分组显示/Carousel模式
            nextButton: '.swiper-button-next',  // 控制按钮
            prevButton: '.swiper-button-prev',
            loop: true,                         //无限循环模式
            autoplay: 3000,                     //自动轮播
            keyboardControl: true,              //键盘控制
            mousewheelControl: false,            //鼠标控制
            autoplayDisableOnInteraction: false,
            speed: 600,
            grabCursor: true
          }
        }
      }
    </code></pre>

    <div class="swiperOption2">
      <swiper :options="swiperOption2">
        <swiper-slide>Slide 1</swiper-slide>
        <swiper-slide>Slide 2</swiper-slide>
        <swiper-slide>Slide 3</swiper-slide>
        <swiper-slide>Slide 4</swiper-slide>
        <swiper-slide>Slide 5</swiper-slide>
        <swiper-slide>Slide 6</swiper-slide>
        <div class="swiper-pagination" slot="pagination"></div>
      </swiper>
    </div>

    <pre class="mt-20"><code>
      swiperOption: {
        pagination: '.swiper-pagination',
        effect: 'cube',
        grabCursor: true,
        loop: true,
        autoplay: 3000,
        autoplayDisableOnInteraction: false,
        cube: {
          shadow: true,
          slideShadows: true,
          shadowOffset: 20,
          shadowScale: 0.94
        }
      }
    </code></pre>

    <swiper class="swiperOption3" :options="swiperOption3">
      <swiper-slide>Slide 1</swiper-slide>
      <swiper-slide>Slide 2</swiper-slide>
      <swiper-slide>Slide 3</swiper-slide>
      <swiper-slide>Slide 4</swiper-slide>
      <swiper-slide>Slide 5</swiper-slide>
      <swiper-slide>Slide 6</swiper-slide>
      <div class="swiper-pagination" slot="pagination"></div>
    </swiper>

    <pre class="mt-20"><code>
      swiperOption: {
        pagination: '.swiper-pagination',
        effect: 'coverflow',
        grabCursor: true,
        centeredSlides: true,
        slidesPerView: 'auto',
        autoplay: 3000,
        loop: true,
        autoplayDisableOnInteraction: false,
        coverflow: {
          rotate: 50,
          stretch: 0,
          depth: 100,
          modifier: 1,
          slideShadows : true
        }
      }
    </code></pre>

    <div class="plugins-tips mt-20 mb-20 border-radius3">
      ElementUI Carousel 走马灯。
      访问地址：<a href="http://element.eleme.io" target="_blank">ElementUI</a>
    </div>

    <el-carousel :interval="4000" arrow="always" type="card" trigger="click" height="200px">
      <el-carousel-item v-for="item in 6" :key="item">
        <h3>{{ item }}</h3>
      </el-carousel-item>
    </el-carousel>
  </div>
</template>

<script>
  import { swiper, swiperSlide } from 'vue-awesome-swiper'
  export default {
    data() {
      return {
        swiperOption: {
          pagination: '.swiper-pagination',
          paginationClickable: true,
          direction: 'horizontal',    // vertical
          spaceBetween: 0,
          slidesPerView: 1,
          nextButton: '.swiper-button-next',
          prevButton: '.swiper-button-prev',
          loop: true,
          autoplay: 3000,
          keyboardControl: true,
          mousewheelControl: false,
          autoplayDisableOnInteraction: false,
          centeredSlides: true,
          speed: 600,
          grabCursor: true
        },

        swiperOption2: {
          pagination: '.swiper-pagination',
          effect: 'cube',
          grabCursor: true,
          loop: true,
          autoplay: 3000,
          autoplayDisableOnInteraction: false,
          cube: {
            shadow: true,
            slideShadows: true,
            shadowOffset: 20,
            shadowScale: 0.94
          }
        },

        swiperOption3: {
          pagination: '.swiper-pagination',
          effect: 'coverflow',
          grabCursor: true,
          centeredSlides: true,
          slidesPerView: 'auto',
          autoplay: 3000,
          loop: true,
          autoplayDisableOnInteraction: false,
          coverflow: {
            rotate: 50,
            stretch: 0,
            depth: 100,
            modifier: 1,
            slideShadows : true
          }
        }
      }
    },

    components: {
      swiper,
      swiperSlide
    }
  }
</script>
